


















































































// 引入外部组件与ts
import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import { Getter } from "vuex-class";

@Component
export default class OptionsEdit extends Vue {
  itemOptions: Array<any> = [
    {
      value: "select",
      label: "单项选择"
    },
    {
      value: "multiSelect",
      label: "多项选择"
    },
    {
      value: "number",
      label: "数值"
    },
    {
      value: "image",
      label: "图片"
    },
    {
      value: "text",
      label: "文本"
    },
    {
      value: "location",
      label: "定位"
    }
  ];
  addOptionsDialog: Boolean = false;
  singleOption: any = {
    argTemplate: {
      options: []
    },
    multiSelectedItems: []
  };
  inspectProjectList: Array<any> = [];

  @Getter("getItemInfo")
  itemInfo!: any;
  @Getter("getItemIndex")
  itemIndex!: any;
  @Getter("getItemMode")
  mode!: any;
  @Getter("getDialogVisible")
  dialogVisible!: any;

  addOptionItem() {
    this.singleOption.argTemplate.options.push({});
  }
  removeOptionItem(item: any) {
    let index = this.singleOption.argTemplate.options.indexOf(item);
    if (index !== -1) {
      this.singleOption.argTemplate.options.splice(index, 1);
    }
  }
  addOptionsToList() {
    this.addOptionsDialog = false;
    if (this.mode === "add") {
      this.inspectProjectList.push(this.singleOption);
    } else {
      this.inspectProjectList[this.itemIndex] = this.singleOption;
    }
    this.$emit("addSingleAttr", this.singleOption); // 返回选项配置结果
    this.$emit("changeVisible", false); // 传回选项配置弹窗显示状态，供下次调用
  }
  changeVisibleFalse() {
    this.addOptionsDialog = false;
    this.$emit("changeVisible", false); // 传回选项配置弹窗显示状态，供下次调用
  }

  mounted() {
    this.addOptionsDialog = this.dialogVisible;
    if (this.mode === "edit") {
      this.singleOption = this.itemInfo;
    } else {
      this.singleOption = {
        argTemplate: {
          options: []
        }
      };
    }
  }
}
