



































































// 引入外部组件与ts
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import { Mutation } from "vuex-class";

@Component
export default class InspectOptionList extends Vue {
  @Prop({ type: Array, required: true })
  optionsList!: Array<any>;
  @Prop({ type: Boolean, required: true, default: false })
  deleteButton!: Boolean;

  transOptions: any = this.optionsList;

  @Mutation("setItemInfo")
  setItemInfo!: any;
  @Mutation("setItemIndex")
  setItemIndex!: any;
  @Mutation("setItemMode")
  setItemMode!: any;
  @Mutation("setDialogVisible")
  setDialogVisible!: any;

  deleteItem(item: any) {
    let index = this.transOptions.indexOf(item);
    if (index !== -1) {
      this.transOptions.splice(index, 1);
    }
    this.$emit("removeOptionItem", index, item);
  }
  editItem(item: any) {
    let index = this.transOptions.indexOf(item);
    this.setDialogVisible(true);
    this.setItemIndex(index);
    this.setItemMode("edit");
    this.setItemInfo(item);
    this.$emit("itemDoEdit", index, item);
  }

  showDefaultChosen(data: any) {
    if (data.itemInputTypeCode === "select") {
      data.argTemplate.options.forEach((radio: any) => {
        if (radio.defaultChosen) {
          data.selectedItem = radio.content;
        }
      });
    } else if (data.itemInputTypeCode === "multiSelect") {
      let arr: Array<any> = [];
      data.argTemplate.options.forEach((radio: any) => {
        if (radio.defaultChosen) {
          arr.push(radio.content);
        }
        (data.multiSelectedItems as Array<any>) = arr;
      });
    }
    return data;
  }

  @Watch("optionsList", { deep: true })
  setOptionsList(newVal: any) {
    this.transOptions = newVal;
    this.transOptions.forEach((attrItem: any) => {
      this.showDefaultChosen(attrItem);
    });
  }
}
