











































































































































// 引入外部组件与ts
import { Component, Prop, Vue } from "vue-property-decorator";
import Utils from "@/utils/utils";
import InspectOptionEdit from "./OptionEdit.vue";
import InspectOptionList from "./OptionList.vue";
import { Getter, Mutation } from "vuex-class";

const Url: any = {};

@Component({
  components: { InspectOptionList, InspectOptionEdit }
})
export default class PlanEditForm extends Vue {
  @Prop({ type: Object, required: true })
  preData!: any;

  inspectTemplate: any = {}; // 基础属性
  inspectProject: Array<any> = [{ attrs: [] }]; // 巡检项表单配置
  resultConfigList: Array<any> = [{}]; // 巡检结果项配置
  resultDeleteIds: Array<any> = []; //巡检结果选项配置，被删除项ids数组
  attrDeleteIds: Array<any> = []; // 表单配置项，被删除项ids数组
  editFormVisible: Boolean = false;
  showDeleteButton: Boolean = false;
  attrsIndex: any = 0;

  // 事件下拉框数据
  eventTypeOptions: Array<any> = [];
  eventTypeProps: any = {
    value: "id",
    label: "eventTypeName",
    children: "children"
  };

  @Getter("getItemInfo")
  itemInfo!: any;
  @Getter("getItemMode")
  mode!: any;
  @Getter("getItemIndex")
  itemIndex!: any;

  @Mutation("setItemMode")
  setItemMode!: any;
  @Mutation("setDialogVisible")
  setDialogVisible!: any;

  // 巡检结果选项配置相关方法
  getEventTypeList() {
    this.$http.get(Url.eventType.getEventTypeTree, { params: { orgId: 0 } }).then((res: any) => {
      if (res.data.status) {
        this.eventTypeOptions = res.data.detail;
        Utils.removeEmptyChildren(this.eventTypeOptions);
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  addResultConfigListItem() {
    this.resultConfigList.push({}); // 增加选项
  }
  removeResultConfigListItem(item: any) {
    let index = this.resultConfigList.indexOf(item); // 删除选项
    if (index !== -1) {
      this.resultConfigList.splice(index, 1);
    }
    this.resultDeleteIds.push(item.id); // 将删除的结果选项id push到服务器请求实体
  }
  static formatConversion(item: any) {
    item.eventId = item.eventIds.join("-"); // 事件选择项数组转为字符转
  }
  addItinerantProjectListItem() {
    this.inspectProject.push({ attrs: [] });
    this.editFormVisible = false;
  }
  removeItinerantProjectListItem(item: any) {
    let index = this.inspectProject.indexOf(item);
    if (index !== -1) {
      this.inspectProject.splice(index, 1);
      item.attrs.forEach((argItem: any) => {
        // 若删除一个巡检项目组，将项目组内所有单项id存入删除数组
        this.attrDeleteIds.push(argItem.id);
      });
    }
  }
  // 1.根据按钮属性（编辑\新增）判断是否将对应选项配置json传入vuex
  // 2.判断选项编辑组件的显示状态
  getOptionEditFromVisible(val: any, index: any) {
    this.setItemMode("add");
    if (this.editFormVisible) {
      this.editFormVisible = val;
      this.setDialogVisible(false);
    } else {
      this.editFormVisible = true;
      this.setDialogVisible(true);
    }
    if (index) {
      this.attrsIndex = index;
    }
  }
  // 将选项编辑组件的结果插入对应巡检项目组
  pushToAttrs(val: any) {
    if (this.mode === "add") {
      this.inspectProject[this.attrsIndex].attrs.push(val);
      this.showDefaultChosen(val);
    } else {
      this.inspectProject[this.attrsIndex].attrs[this.itemIndex] = val;
      this.showDefaultChosen(val);
    }
  }
  // 删除单个巡检项目
  deleteItemInAttrs(index: any, item: any) {
    this.inspectProject[this.attrsIndex].attrs.splice(index, 1);
    this.attrDeleteIds.push(item.id);
  }
  // 编辑单个巡检项目
  editItemInAttrs() {
    this.editFormVisible = true;
  }
  saveTemplateForms() {
    this.$http
      .post(Url.inspect.saveTemplate, {
        inspectProject: this.inspectProject,
        inspectTemplate: this.inspectTemplate,
        resultConfigList: this.resultConfigList,
        resultDeleteIds: this.resultDeleteIds,
        attrDeleteIds: this.attrDeleteIds
      })
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success("方案保存成功！");
          this.turnToPlanTree();
        } else {
          this.$message.success(res.data.message);
        }
      });
  }
  turnToPlanTree() {
    this.$emit("turnToPlanTree", true);
  }
  getPlanInfo() {
    this.$http.get(Url.inspect.getTemplateById + this.inspectTemplate.id).then((res: any) => {
      if (res.data.status) {
        this.inspectTemplate = res.data.data.inspectTemplate;
        this.inspectProject = res.data.data.inspectProject;
        this.resultConfigList = res.data.data.resultConfigList;
        this.resultConfigList.forEach((item: any) => {
          let arr = [];
          if (item.eventId) {
            let i = item.eventId.split("-");
            for (let k = 0; k < i.length; k++) {
              let int = parseInt(i[k]);
              arr.push(int);
            }
          }
          item.eventIds = arr;
        });
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  showDefaultChosen(data: any) {
    if (data.itemInputTypeCode === "select") {
      data.argTemplate.options.forEach((radio: any) => {
        if (radio.defaultChosen) {
          data.selectedItem = radio.content;
        }
      });
    } else if (data.itemInputTypeCode === "multiSelect") {
      let arr: Array<any> = [];
      data.argTemplate.options.forEach((radio: any) => {
        if (radio.defaultChosen) {
          arr.push(radio.content);
        }
        (data.multiSelectedItems as Array<any>) = arr;
      });
    }
    return data;
  }

  mounted() {
    this.inspectTemplate.catalogId = this.preData.catalogId;
    if (this.preData.mode === "preview") {
      this.inspectTemplate.id = this.preData.planId;
      this.showDeleteButton = false;
      this.getPlanInfo();
    } else if (this.preData.mode === "edit") {
      this.inspectTemplate.id = this.preData.planId;
      this.showDeleteButton = true;
      this.getPlanInfo();
    } else {
      this.inspectTemplate.id = "";
      this.showDeleteButton = true;
    }
    // this.getEventTypeList();
  }
}
