





































































































// 引入外部组件与ts
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import PlanEditForm from "./PlanEditForm.vue";
import Url from "../api";
import PlanEdit from "@/microApps/inspection/components/PlanEdit.vue";

@Component({
  components: { PlanEdit, PlanEditForm }
})
export default class PlansTree extends Vue {
  @Prop({ type: String, required: true, default: "" })
  mode!: String;

  @Prop({ type: Number, default: 0 })
  id!: any;

  plansName: String = "";
  manageButtonsVisible: Boolean = false;
  selectButtonsVisible: Boolean = false;
  planEditFormVisible: Boolean = false;

  loading: Boolean = false;
  plansData: Array<any> = [];
  plansProp: any = {
    children: "children",
    label: "catalogName"
  };
  templatesList: Array<Object> = [];
  editMode: any = {
    planId: 0,
    catalogId: "",
    mode: "add" // or edit/preview
  };

  selected: Number = 0;
  chosenItem: any = {};
  addNodeFormVisible: boolean = false;
  nodeForm: any = {};

  planView(id: any, catalogId: any) {
    this.editMode.mode = "review";
    this.editMode.planId = id;
    this.editMode.catalogId = catalogId;
    this.planEditFormVisible = true;
  }
  planEdit(id: any, catalogId: any) {
    this.editMode.mode = "edit";
    this.editMode.planId = id;
    this.editMode.catalogId = catalogId;
    this.planEditFormVisible = true;
  }
  handleDelete(id: any) {
    this.$http.get(Url.deleteTemplate + id).then((res: any) => {
      if (res.data.status) {
        this.$message.success("删除成功");
      } else {
        this.$message.error(res.data.message);
      }
    });
    this.getCatalogTree();
  }
  createNewPlan() {
    this.editMode.mode = "add";
    this.planEditFormVisible = true;
  }
  selectedItem(item: any) {
    this.selected = item.id;
    this.$emit("inspectPlanInfo", item);
  }
  changeInspectPlanVisible(val: any) {
    this.planEditFormVisible = !val;
    this.handleNodeClick(this.chosenItem);
  }

  // 修改开始
  // 获取目录树
  async getCatalogTree() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(`${Url.catalog.typeTree}INSPECT_PlAN_CODE`);
      if (status) {
        this.plansData = [];
        this.plansData.push(data);
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return "error";
    }
  }
  /**
   * 打开添加目录节点弹窗
   * @param mode add:添加下级目录节点；edit：编辑当前节点
   * @param data 当前目录节点信息
   * @param node 当前节点信息，用于获取父节点信息
   **/
  openNodeForm(mode: string = "add", data: any = null, node: any = null) {
    console.log(node);
    if (mode === "edit") {
      this.nodeForm = data;
      this.nodeForm.parentName = node.parent.data.catalogName;
    } else {
      if (data) {
        this.nodeForm.parentId = data.id;
        this.nodeForm.parentName = data.catalogName;
        this.nodeForm.catalogType = "INSPECT_PlAN_CODE";
      } else {
        this.nodeForm = {
          catalogType: "INSPECT_PlAN_CODE",
          parentId: null,
          parentName: null
        };
      }
    }
    this.addNodeFormVisible = true;
  }
  // 保存目录节点
  async saveNode() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.catalog.add, this.nodeForm);
      if (status) {
        this.$message.success("保存成功");
      } else {
        this.$message.error(message);
      }
      this.addNodeFormVisible = false;
      this.getCatalogTree();
    } catch (e) {
      return "error";
    }
  }
  // 目录节点单击事件
  async handleNodeClick(item: any) {
    this.loading = true;
    this.editMode.catalogId = item.id;
    this.chosenItem = item;
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.template.list + item.id);
    if (status) {
      this.plansName = item.catalogName;
      this.templatesList = data;
    } else {
      this.$message.error(message);
    }
    this.loading = false;
  }
  // 删除目录节点
  async removeTreeNode(item: any) {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(Url.catalog.delete + item.id);
    } catch (e) {
      console.log(e);
    }
  }
  /**
   * 打开方案 编辑/新增 页面
   * @param mode add:添加方案;edit:编辑当前方案;preview:查看当前方案
   * @param id 当前方案id
   * @param catalogId 当前选择目录id
   **/
  openPlanEditForm(mode: string = "add", id: string = "", catalogId: string = "") {
    console.log(catalogId);
    if (catalogId) {
      this.planEditFormVisible = true;
      if (mode === "add") {
        this.editMode.mode = "add";
      } else {
        this.editMode.mode = mode;
        this.editMode.planId = id;
        this.editMode.catalogId = catalogId;
      }
    } else {
      this.$message.warning("请先选择方案！");
    }
  }

  created() {
    // 按钮等状态
    this.manageButtonsVisible = this.mode === "manage";
    this.selectButtonsVisible = this.mode === "choose";
    this.selected = this.id ? this.id : 0;
    this.getCatalogTree();
  }
}
