import { Vue, Component, Prop } from "vue-property-decorator";
import Url from "../api";
import AddInspectItem from "@/microApps/inspection/components/AddInspectItem.vue";

@Component({
  components: { AddInspectItem }
})
export default class PlanEdit extends Vue {
  @Prop({ type: String, default: "add" })
  mode!: string;

  baseFrom: any = {};
  resultForm: { result: any[] } = { result: [] };
  inspectProject: { inspects: { attrs: any[]; groupName: string }[] } = {
    inspects: []
  };

  // 事件下拉框数据
  eventTypeOptions: Array<any> = [];
  eventTypeProps: any = {
    value: "id",
    label: "name",
    children: "children"
  };

  optionItem: any = {};
  dialogVisible: boolean = false;
  currentIndex: number = 0;

  async getEventOptions() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(Url.other.getAllEventType);
      if (status) {
        this.eventTypeOptions = data;
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      console.log(e);
    }
  }

  addResultListItem() {
    this.resultForm.result.push({}); // 增加选项
  }
  removeResultListItem(item: any) {
    let index = this.resultForm.result.indexOf(item); // 删除选项
    if (index !== -1) {
      this.resultForm.result.splice(index, 1);
    }
    // this.resultDeleteIds.push(item.id); // 将删除的结果选项id push到服务器请求实体
  }

  addItinerantProjectListItem(status: boolean, index: number) {
    this.dialogVisible = true;
    this.currentIndex = index;
    this.optionItem = {
      id: "",
      baseTypeId: "",
      code: "",
      attrName: "",
      attrType: "",
      sort: "",
      dataRange: {
        options: [],
        multiSelectedItems: []
      }
    };
  }
  removeItinerantProjectListItem(item: any) {}
  initInspectItem(data: any) {
    this.inspectProject.inspects[this.currentIndex].attrs.push(data);
  }

  removeItinerantProject(item: any) {}

  created() {
    // if (this.mode === "add") {
    this.resultForm.result = [{ name: "", eventId: "" }];
    this.inspectProject.inspects = [{ groupName: "", attrs: [] }];
    // }
    this.getEventOptions();
  }
}
